<template>
 <div id="detailnav" >
     <Nav >
         <div slot="center" class="title">
             <div v-for="(item,index) in title"  
             class="item"
             :class="{active:cindex===index}"
              @click="tabclick(index)">
              {{item}}
              </div>
         </div>
         <div slot="left" class="back" @click="backclick">
             <img src="~assets/img/common/back.svg" alt="">
         </div>
     </Nav>
 </div>
 
</template>

<script>
import Nav from "components/common/nav.vue"

export default {
  name: 'detailnav',
  data(){
      return{
          title:['商品','参数','评论','推荐'],
          cindex:0
      }
  },
  methods:{
      tabclick(index){
          this.cindex=index,
          this.$emit('tabclick',index)
        
      },
      backclick(){
        this.$router.back()

      }
  }
  ,
  
  components: {
      Nav
  }
}
</script>

<style scoped>
#detailnav{
    position: fixed;
    background-color:#fff;
    left: 0;
    right: 0;
    top: 0;
}
.title{
    display: flex;
}
.item{
    flex: 1;
}
.active{
    color: var(--color-high-text)
    
}
.back img {
    margin-top: 12px;
  }

</style>