<template>
  <div id="detail">

    <detailnav class="detail-nav" @tabclick='tabclick' ref="nav" />

    <scroll class="content" ref="scroll" :probe-type='2' @scroll="cscroll">
      <div>{{$store.state.carlist.length}}</div>

      <detailswip :topimg="topimg" />
      <detail-base-info :goods="goods" />
      <detail-shop-info  :shop="shop" />
      <detail-goods-info :detail-info="detailInfo" @imageLoad="imageLoad" />
      <detail-param-info ref="params" :param-info="paramInfo" />

    </scroll>
      <detailbuttom @add="addto" />
    <back-top @click.native="backClick" v-show="isShowBackTop"/>
  </div>
</template>

<script>
import detailnav from "./detailnav.vue";
import detailswip from "./detailswip.vue";
import DetailBaseInfo from "./DetailBaseInfo.vue";
import DetailShopInfo from "./DetailShopInfo";
import DetailGoodsInfo from "./DetailGoodsInfo";
import DetailParamInfo from "./DetailParamInfo";
import detailbuttom from "./detailbuttom";
import BackTop from "components/content/backTop/BackTop.vue"

import Scroll from "components/common/scroll/Scroll.vue";
import {debounce} from '../../common/utils'
import { detaildata, Goods, Shop, GoodsParam } from "network/detail";

export default {
  name: "detail",
  data() {
    return {
      iid: null,
      topimg: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      topy:[],
      getyopy:null,
      cindex:0,
      isShowBackTop: false,



    };
  },
  created() {
    this.iid = this.$route.params.iid;
    detaildata(this.iid).then((res) => {
      const data = res.result;
      this.topimg = data.itemInfo.topImages;
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      this.shop = new Shop(data.shopInfo);
      this.detailInfo = data.detailInfo;
      this.paramInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      );
    });
      this.getyopy=debounce(()=>{
      this.topy=[]
      this.topy.push(0)
      this.topy.push(this.$refs.params.$el.offsetTop)
      console.log(this.topy)

      })
    
  },

  methods: {
    imageLoad() {
      this.$refs.scroll.refresh()
      // this.getyopy
      this.getyopy()
    },
    tabclick(index){
  
      this.$refs.scroll.scrollTo(0,-this.topy[index],100)
    },
    cscroll(position){
      let lengths=this.topy.length
      const positiony=-position.y
      for(let i in this.topy){
        i=i*1
        if (this.cindex!==i &&  ((positiony>=this.topy[i] && positiony<this.topy[i+1]) || (i===lengths-1 && positiony>=this.topy[i]))){
          this.cindex=i
          console.log(this.cindex)
          this.$refs.nav.cindex=this.cindex

        }
      }
        this.isShowBackTop = (-position.y) > 1000

    },
    backClick() {
        this.$refs.scroll.scrollTo(0, 0)
      },
      addto(){
        const product={}
        product.img=this.topimg[0]
        product.desc=this.goods.desc
        product.title=this.goods.title
        product.prize=this.goods.nowprize
        product.iid=this.iid
        this.$store.dispatch('addcart',product)

      }
    
    

  },
  components: {
    detailnav,
    detailswip,
    detaildata,
    Goods,
    DetailBaseInfo,
    Shop,
    DetailShopInfo,
    Scroll,
    DetailGoodsInfo,
    DetailParamInfo,
    GoodsParam,
    debounce,
    detailbuttom,
    BackTop
  },
};
</script>

<style scoped>
#detail {
  position: relative;
  z-index: 9;
  background-color: #fff;
  height: 100vh;
}
.detail-nav {
  position: relative;
  z-index: 9;
  background-color: #fff;
}

.content {
  height: calc(100% - 44px);
}
</style>